.App{
    text-align:center;
}
body{
  text-align:center;
}
.slider {

    height:1000px; 
  
    background-image: url('../public/img/slider.jpg');
  
    background-size: cover;
  
    background-position: center;
  
  }
  	
.c1 {text-decoration: none; color:#333; text-align: center;}

.c1:hover {color: red;}

.start{
  opacity:0;
  transform:scale(0);
}

.end{
  opacity:1;
  transition: opacity 0.5s;
  
  transform:scale(1);
  transition: all 0.5s;
}

.before{
  opacity:0;
  transform:scale(0);
}

.after{
  opacity:1;
  transition: opacity 0.5s;
  
  transform:scale(1);
  transition: all 0.5s;
}
